// app/@modal/(.)notes/[id]/NotePreview.client.tsx
'use client';

import { useQuery } from '@tanstack/react-query';
import { useRouter } from 'next/navigation';
import { fetchNoteById } from '@/lib/api';
import NotePreview from '@/components/NotePreview/NotePreview';
import Modal from '@/components/Modal/Modal';
import Loader from '@/components/Loader/Loader';
import ErrorMessage from '@/components/ErrorMessage/ErrorMessage';

// 1. Компонент знову приймає 'id' як пропс
interface ModalNotePreviewProps {
  id: string;
}

export default function ModalNotePreview({ id }: ModalNotePreviewProps) {
  const router = useRouter();

  const {
    data: note,
    status,
  } = useQuery({
    // 2. Ключ запиту змінено на 'note'
    queryKey: ['note', id],
    queryFn: () => fetchNoteById(id!),
    enabled: !!id,
    refetchOnMount: false,
  });

  // Функція закриття для передачі в Modal
  const handleClose = () => {
    router.back();
  };

  let content: React.ReactNode;

  if (status === 'pending') {
    content = <Loader />;
  }
  if (status === 'error') {
    content = <ErrorMessage>Could not fetch note details.</ErrorMessage>;
  }
  if (status === 'success' && note) {
    content = <NotePreview note={note} />;
  }

  // 3. Компонент сам рендерить Modal і передає йому onClose
  return <Modal onClose={handleClose}>{content}</Modal>;
}